.ant-table-tbody > tr > td {
  padding: 0;
}
.ant-table-thead > tr > th {
  padding: 1.6rem 1rem;
}

.antd-link {
  display: block;
  padding: 1rem 1rem;
  height: 100%;
  cursor: pointer;
}

.loading {
  margin-bottom: 20px;
  padding: 30px 50px;
  background: rgba(0, 0, 0, 0.01);
  border-radius: 4px;

  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

// ---------------------Tabs Antd------------------------------------
.ant-spin-dot-item {
  width: 3rem;
  height: 3rem;
}

.ant-spin-dot {
  font-size: 4rem;
  width: 6rem;
  height: 6rem;
}

.ant-tabs-top > .ant-tabs-nav {
  margin: 0;
}

.ant-tabs > .ant-tabs-nav .ant-tabs-nav-list {
  width: 100%;
}

.ant-tabs-tab {
  width: 100%;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab {
  width: calc(100% * 4 / 12);
  padding: 1.2rem;

  border-left: none;
  border-right: none;
}

.ant-tabs-card.ant-tabs-top > .ant-tabs-nav .ant-tabs-tab + .ant-tabs-tab {
  margin-left: 0;
}

.ant-tabs-tab:hover,
.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #763cc9;
}

.ant-tabs-tab-btn {
  text-align: center;
  font-size: 1.6rem;
  font-weight: 500;
  width: 100%;
}

.ant-tabs-ink-bar {
  background: #4f3cc9;
}

// ---------------------Ckeditor------------------------------------
.ck {
  cursor: pointer;
}

.ck.ck-sticky-panel__content {
  z-index: 2;
}

.ck.ck-editor__editable_inline {
  border: none;
  border-bottom: 1px solid #8e8e8e;
}

.ck-rounded-corners .ck.ck-editor__top .ck-sticky-panel .ck-toolbar {
  border: none;
  background-color: #fff;
}

.ck.ck-editor__main > .ck-editor__editable.ck-editor__editable {
  background-color: #f5f5f5;
  position: relative;
  min-height: 15rem;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 2px;
    background-color: #4f3cc9;
    transform: scale(0);
    transition: all 0.3s ease-in-out;
  }

  &:focus::after {
    transform: scale(1);
  }
}

.ck.ck-editor__editable:not(.ck-editor__nested-editable).ck-focused {
  background-color: #dedede;
  border: none;
  box-shadow: initial;
}

// -------------------------------------Modal form image------------------------------------------------------------
.ant-modal-body {
  padding: 15px;
}

.ant-modal-body .modal-image {
  width: 100%;
  height: 45rem;
  object-fit: cover;
  object-position: center;
}

// --------------------------------------------Pagination------------------------------------------------------------------
.ant-pagination-options {
  display: none;
}

.ant-table-pagination-right {
  justify-content: center;
}
